import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import dfaultLayout from "./layouts/default";
import jsbridgeLayout from "./layouts/jsbridge";
Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/dapp",
      name: "dapp",
      component: jsbridgeLayout,
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      children: [
        {
          path: "/dapp/transfer",
          name: "transfer",
          component: dfaultLayout,
          children: [
            {
              path: "/dapp/transfer/list",
              name: "tansfer-list",
              component: () => import("./views/transfer/list")
            },
            {
              path: "/dapp/transfer/detail",
              name: "tansfer-detail",
              component: () => import("./views/transfer/detail")
            },
            {
              path: "/dapp/transfer/contract",
              name: "tansfer-contract",
              component: () => import("./views/transfer/contract")
            }
          ]
        }
      ]
    }
  ]
});
