// define('mt/Valyria', [], function(){
const Valyria = function () {
    var Valyria = function () { };

    /**
     * Compare the app version
     * -1 : version1 < version2
     * 0  : version1 = version2
     * 1  : version1 > version2
     * @param version1
     * @param version2
     */
    Valyria.prototype.appVersionCompare = function (version1, version2) {
        var versionParts1 = version1.split('.');
        var versionParts2 = version2.split('.');

        var index = 0;
        while (true) {
            if (index < versionParts1.length && index < versionParts2.length) {
                if (versionParts1[index] < versionParts2[index]) {
                    return -1;
                }

                if (versionParts1[index] > versionParts2[index]) {
                    return 1;
                }

                if (versionParts1[index] == versionParts2[index]) {
                    index++;
                }
            } else if (index >= versionParts1.length && index >= versionParts2.length) {
                return 0;
            } else {
                if (index < versionParts1.length) {
                    for (var i = index; i < versionParts1.length; i++) {
                        if (versionParts1[i] > 0) {
                            return 1;
                        }
                    }
                    return 0;
                } else {
                    for (i = index; i < versionParts2.length; i++) {
                        if (versionParts2[i] > 0) {
                            return -1;
                        }
                    }
                    return 0;
                }
            }
        }
    };

    return new Valyria();
}


/**
 * The basic interface of native invocation.
 */
// define('mt/Snow', ['mt/Westeros'], function(Westeros) {
const Snow = (function () {
    var Snow = function () { };

    if (Westeros.versions.android) {
        Snow.prototype.invokeNative = function (args) {
            prompt('snow://' + JSON.stringify(args), '');
        };
    } else if (Westeros.versions.ios) {
        if (Westeros.versions.ios_wkwebview) {
            Snow.prototype.invokeNative = function (args) {
                window.webkit.messageHandlers.snow.postMessage(args);
            };
        } else {
            Snow.prototype.invokeNative = function (args) {
                var iFrame = document.createElement('iframe');
                iFrame.setAttribute('src', 'snow://' + encodeURIComponent(JSON.stringify(args)).replace(/\*/g, '%2A').replace(/!/, '%21'));
                document.documentElement.appendChild(iFrame);
                iFrame.parentNode.removeChild(iFrame);
                iFrame = null;
            };
        }
    } else {
        Snow.prototype.invokeNative = function () {
            console.error('调用异常');
        };
    }

    return new Snow();
})()


